import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './pages/home/home.component';
import { NgModule } from '@angular/core';
import { ShoppingLocationComponent } from './pages/home/shopping-location/shopping-location.component';


export const PAGE_COMPONENTS = [HomeComponent, ShoppingLocationComponent];

const appRoutes: Routes = [
  { path: 'home', component: HomeComponent },
  { path: 'localtion/:id', component: ShoppingLocationComponent },
  { path: '',
    redirectTo: '/home',
    pathMatch: 'full'
  },
];

@NgModule({
  imports: [
    RouterModule.forRoot(
      appRoutes,
      {
        useHash: true,
        enableTracing: false, // <-- debugging purposes only
      }
    )
  ],
  exports: [
    RouterModule
  ]
})
export class AppRoutingModule { }
