import { Component, OnInit, AfterViewChecked } from '@angular/core';
import * as svgPanZoom from 'svg-pan-zoom';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-shopping-location',
  templateUrl: './shopping-location.component.html',
  styleUrls: ['./shopping-location.component.css']
})
export class ShoppingLocationComponent implements OnInit, AfterViewChecked {
  colorObj = {
    color1: '',
    color2: '',
    color11: '',
    color22: '',
  }
  constructor(
    private route: ActivatedRoute,
  ) { }

  ngOnInit() {
    this.route.params.subscribe(params => {
      if (params.id === '1') {
        this.colorChange('color1', 'color2')
      }

      if (params.id === '2') {
        this.colorChange('color11', 'color22')
      }
    })
  }
  colorChange(name1, name2) {
    for ( const item in this.colorObj) {
      if (this.colorObj.hasOwnProperty(item)) {
        this.colorObj[item] = '';
      }
    }
    this.colorObj[name1] = 'color1';
    this.colorObj[name2] = 'color2';
  }
  ngAfterViewChecked(): void {
    // const svgPan = svgPanZoom('#localtion-svg', {
    //   zoomEnabled: true,
    //   controlIconsEnabled: false,
    //   fit: true,
    //   center: true,
    // });

    // svgPan.zoomAtPointBy({x: 500, y: 500});
  }
}
